body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'OrangeDays';
  src: url("../../static/media/Orange Days.ef1fb2c0.ttf");
}

a:link { text-decoration: none; }

a:visited { text-decoration: none; }

a:hover { text-decoration: none; }

a:active { text-decoration: none; }

.header {
  min-height: 7vh;
  min-width: 100vh;
  background-color: #292929;
}

.App {
  text-align: center;
  background-color: #292929;
  min-height: 93vh;
  min-width: 100vh;
  margin: 0;
  padding: 0;
}

.title {
  color: #ececec;
  padding: 100px;
  font-family: "OrangeDays", sans-serif;
  font-size: 6rem;
  -webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
          filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
}

.content {
  height: 500px;
  margin-top: 150px;
  display: flex;
  justify-content: center;
}

.item{
  -webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
          filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
  margin-left: 150px;
  margin-right: 150px;
  height: 80px;
  width: 80px;
  display: flex;
  flex-direction: column;
}

.image-link {
  margin-left: -100%;
  margin-right: -100%;
  text-align: center;
}

.item-icon {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  height: 5rem;
  width: 5rem;
  margin-left: 5px;
  margin-right: 5px;
}

.item-text {
  color: #ececec;
  font-family: "OrangeDays", sans-serif;
  font-size: 2rem;
}

.glow:hover {
  -webkit-filter: drop-shadow(0px 0px 5px rgb(255, 255, 255));
          filter: drop-shadow(0px 0px 5px rgb(255, 255, 255));
  transition: all 0.5s ease;
}

.socials{
  -webkit-filter: drop-shadow(5px 5px 5px #222);
          filter: drop-shadow(5px 5px 5px #222);
  position: absolute;
  top: 20px;
  right: 20px;
}

.resume{
  -webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
          filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
  position: absolute;
  top: 20px;
  left: 20px;
}

.logo-icon {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  height: 40px;
  width: 40px;
  margin-left: 5px;
  margin-right: 5px;
}

.footer{
  background-color: #292929;
  height: 4rem;
  min-width: 100vh;
  margin: 0;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
}

.license {
  color: #ececec;
  font-size: 1rem;
}

.return-button{
  position: absolute;
  left: 20px;
  top: 20px;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  color: #ececec;
}

.webcrawlerapp {
  text-align: center;
  background-color: #292929;
  min-height: 1250px;
  min-width: 100vh;
  margin: 0;
  padding: 0;
}

.huffmanapp {
  text-align: center;
  background-color: #292929;
  min-height: 1700px;
  min-width: 100vh;
  margin: 0;
  padding: 0;
}

.httpserverapp {
  text-align: center;
  background-color: #292929;
  min-height: 1400px;
  min-width: 100vh;
  margin: 0;
  padding: 0;
}

.pagetitle {
  color: #ececec;
  padding: 50px;
  font-family: "OrangeDays", sans-serif;
  font-size: 6rem;
  -webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
          filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
}

.overview{
  height: 60px;
  max-width: 1050px;
  color: #ececec;
  text-align: left;
  margin: auto;
}

.description {
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight:normal;
}

.vidsetting{
  height: 600px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 }

